"use client"; // nÃ©cessaire pour utiliser useState

import { useState } from "react";
import { FaChevronLeft, FaChevronRight } from "react-icons/fa";

export default function Actions() {
  const actions = [
    { 
        title: "Ã‰ducation", 
        desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit." },

    { 
        title: "Environnement", 
        desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit." },

    { 
        title: "SantÃ©", desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit." },
    { title: "Agriculture", desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit." },
    { title: "Culture", desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit." },
  ];

  const [currentIndex, setCurrentIndex] = useState(0);

  const prevSlide = () => {
    setCurrentIndex((prev) => Math.max(prev - 1, 0));
  };

  const nextSlide = () => {
    setCurrentIndex((prev) => Math.min(prev + 1, actions.length - 3));
  };

  return (
    <div className="relative">
      {/* FlÃ¨che gauche */}
      <button 
        onClick={prevSlide} 
        className="absolute left-0 top-1/2 -translate-y-1/2 bg-white p-2 rounded-full shadow hover:bg-green-100 z-10"
      >
        <FaChevronLeft className="text-green-700" />
      </button>

      {/* Blocs */}
      <div className="flex overflow-hidden">
        <div className="flex transition-transform duration-300" style={{ transform: `translateX(-${currentIndex * (100/3)}%)` }}>
          {actions.map((action, idx) => (
            <div key={idx} className="min-w-[33.3333%] px-3">
              <div className="bg-white shadow-md rounded-2xl p-6 h-60 flex flex-col items-center justify-start hover:shadow-lg transition">
                <div className="text-4xl mb-4 text-green-700">ðŸ“š</div>
                <h3 className="text-xl font-semibold text-green-700 mb-2">{action.title}</h3>
                <p className="text-gray-600 text-sm">{action.desc}</p>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* FlÃ¨che droite */}
      <button 
        onClick={nextSlide} 
        className="absolute right-0 top-1/2 -translate-y-1/2 bg-white p-2 rounded-full shadow hover:bg-green-100 z-10"
      >
        <FaChevronRight className="text-green-700" />
      </button>
    </div>
  );
}
